import React, { Component } from 'react'

export default class deleteButton extends Component {
    constructor(props){
        super(props)
    }
    render() {
        return (
            <div className='col-4'>
                <button
                    onClick={() => this.props.deleteItem(this.props.list.id)}
                    className="btn btn-danger"
                >
                    X
                </button>
            </div>
        )
    }
}
