import React from 'react';
import './App.css'
import CheckBox from './CheckBox'
import DeleteButton from './deleteButton'
import 'bootstrap/dist/css/bootstrap.css';

export default class ToDoList extends React.Component {
    constructor(props) {
        super(props);
        this.props= {
            list:[]
        }
        this.state = {
            list: [],
            text: '',
            total: 0
        };
        this.addItem = this.addItem.bind(this);
        this.updateInput = this.updateInput.bind(this);
        this.deleteItem = this.deleteItem.bind(this);
        this.clearAll = this.clearAll.bind(this);
    };

    updateInput(key, value) {
        this.setState({
            [key]: value
        })
    };

    addItem() {
        //create item 
        if (this.state.text.slice() !== '') {
            const text = {
                id: 1 + Math.random(),
                value: this.state.text.slice()
            };
            //copy of current list items  && ids
            const list = [...this.state.list]

            //add new item to the list
            list.push(text);
            console.log(text.id)

            this.setState({
                list,
                text: "",
                total: this.state.list.length + 1,
            })

        }
    }










    deleteItem(id) {

        //current copy of the list of items
        const list = [...this.state.list]

        // filter out the item by item id 
        const updatedList = list.filter(item => item.id !== id);

        this.setState({
            list: updatedList,
            total: this.state.list.length - 1,

        })
    };




    clearAll(){
        const clearedList = [];

        this.setState({
            list: clearedList,
            total: 0,
            checked: false,
        })
    };








    render() {
        return (
            <div className='todoListContainer'>
                <div className="container-fluid d-flex justify-content-center mt-5 toDoList">
                    <div className="row border">
                    {/* this is the basic user interface. it contains the 
                    clear all button, the title, a total count of the todos, 
                    the user input, and a button to add the user input. */}
                        <button type='button' className='col-2 btn btn-danger' onClick={this.clearAll}>Clear all</button>
                        <h1 className="text-center col-6">To-do List:</h1>
                        <h2 className="col-4">{this.state.total} total</h2>
                        <input
                            className='offset-2 col-4'
                            type="text"
                            value={this.state.text}
                            placeholder="task"
                            onChange={event => this.updateInput("text", event.target.value)}
                        />
                        <button className="col-4" onClick={this.addItem}>Add Item</button>
                        {/* this is where I create my list of todos, 
                        it should contain the text input from the user, 
                        a checkbox to click if its done or not, and an 
                        option to remove it from the list. each input 
                        will generate a new toDoListItem component*/}
                        <div className='col-12'>
                            <ul className='p-2 list-unstyled'>
                                {this.state.list.map(item => {
                                    return (
                                        <li
                                            className='listSpacing py-2 d-flex'
                                            // justify-content-around'
                                            key={item.id}
                                        >
                                            <CheckBox/>

                                            <p className="col-4 text-wrap">
                                                {item.value}
                                            </p>
                                            <DeleteButton/>
                                            {/* <button
                                                onClick={() => this.deleteItem(item.id)}
                                                className="offset-3 col-1 btn btn-danger"
                                            >
                                                X
                                            </button> */}
                                        </li>
                                    )
                                })}
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}